              this.currentSuiMethod == "sui_signAndExecuteTransaction"
            ) {
              suiResult = await adapter.signAndExecuteTransaction(
                this.currentSuiTransaction
              );
              this.testboc = JSON.stringify(suiResult);
            } else if (this.currentSuiMethod == "sui_signMessage") {
              console.log("signMessage----", this.currentSuiTransaction);
              suiResult = await adapter.signMessage(this.currentSuiTransaction);
              this.testboc = JSON.stringify(suiResult);
            } else if (this.currentSuiMethod == "sui_signPersonalMessage") {
              console.log(
