            );
            if (typeof result == "string") {
              this.testboc = result;
            } else {
              this.testboc = JSON.stringify(result);
            }
            return
          }

          if (chainId == 'btc:mainnet') {
            const btcAdapter = new OKXBtcProvider(window.provider)
            var btcResult = 'invalid result'

            if (this.currentBtcMethod == 'btc_signMessage') {
