              this.testboc = JSON.stringify(btcResult)
            } else if (this.currentBtcMethod == 'btc_sendInscription') {
              btcResult = await btcAdapter.sendInscription(chainId, this.currentBtcTransaction.address, this.currentBtcTransaction.inscriptionId, this.currentBtcTransaction.options)
              this.testboc = JSON.stringify(btcResult)
            } else if (this.currentBtcMethod == 'btc_signAndPushPsbt') {
              btcResult = await btcAdapter.signAndPushPsbt(chainId, this.currentBtcTransaction.psbtHex, this.currentBtcTransaction.options)
              this.testboc = JSON.stringify(btcResult)
            }else if (this.currentBtcMethod == 'btc_sendBitcoin') {
              btcResult = await btcAdapter.sendBitcoin(chainId, this.currentBtcTransaction.toAddress, this.currentBtcTransaction.satoshis, this.currentBtcTransaction.options)

              this.testboc = JSON.stringify(btcResult)
            }
            return
          }

          if (chainId == 'fractal:mainnet') {
            const btcAdapter = new OKXBtcProvider(window.provider)
            var fractalResult = 'invalid result'

            if (this.currentFractalMethod == 'fractal_signMessage') {
              fractalResult = await btcAdapter.signMessage(chainId, this.currentFractalTransaction['signStr'])
              this.testboc = JSON.stringify(fractalResult)
            } else if (this.currentFractalMethod == 'fractal_send') {
              fractalResult = await btcAdapter.send(chainId, this.currentFractalTransaction)
              this.testboc = JSON.stringify(fractalResult)
            } else if (this.currentFractalMethod == 'fractal_signPsbt') {
              fractalResult = await btcAdapter.signPsbt(chainId, this.currentFractalTransaction.psbtHex, this.currentFractalTransaction.options)
              this.hexToBase64(fractalResult).then(bs64Str => {
                this.testboc = JSON.stringify(bs64Str)
              }).catch(error => {
                this.testboc = error
              })
            } else if (this.currentFractalMethod == 'fractal_signPsbts') {
              fractalResult = await btcAdapter.signPsbts(chainId, this.currentFractalTransaction.psbtHexs, this.currentFractalTransaction.options)
              this.testboc = JSON.stringify(fractalResult)
            } else if (this.currentFractalMethod == 'fractal_sendInscription') {
              fractalResult = await btcAdapter.sendInscription(chainId, this.currentFractalTransaction.address, this.currentFractalTransaction.inscriptionId, this.currentFractalTransaction.options)
              this.testboc = JSON.stringify(fractalResult)
            } else if (this.currentFractalMethod == 'fractal_signAndPushPsbt') {
              fractalResult = await btcAdapter.signAndPushPsbt(chainId, this.currentFractalTransaction.psbtHex, this.currentFractalTransaction.options)
              this.testboc = JSON.stringify(fractalResult)
            }else if (this.currentFractalMethod == 'fractal_sendBitcoin') {
              fractalResult = await btcAdapter.sendBitcoin(chainId, this.currentFractalTransaction.toAddress, this.currentFractalTransaction.satoshis, this.currentFractalTransaction.options)

              this.testboc = JSON.stringify(fractalResult)
            }

            return
          }

          console.log(`请求数据发送 sendTransaction ----->> ${JSON.stringify(sendData)}`)
          var result = await window.provider.request(sendData, chainId)
          console.log(`请求数据回调 sendTransaction ----->> ${JSON.stringify(result)}`)

          console.log("windowOpen[click_3]: ", Date.now());
          if (typeof result == "string") {
            this.testboc = result;
          } else {
            this.testboc = JSON.stringify(result);
          }
          console.log("方法数据回调 --this.testboc--", this.testboc);
        } catch (error) {
          console.error("方法数据回调失败 --Error--", error.message);

          if (error.code === OKX_CONNECT_ERROR_CODES.USER_REJECTS_ERROR) {
            this.testboc = error.message;
          } else {
            this.testboc = "签名错误！" + error.message;
          }
        }
      } else {
        alert("交易数据为空，请选则或输入交易数据。");
      }
    },

    hexToBase64(hexStr) {
      var bytes = [];
      for (var i = 0; i < hexStr.length; i += 2) {
        bytes.push(parseInt(hexStr.substr(i, 2), 16));
      }
      const buffer = new Uint8Array(bytes)
      const blob = new Blob([buffer]);
      return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.onload = function (e) {
          var base64Data = e.target.result.split(",")[1]
          resolve(base64Data);
        };
        reader.onerror = function (e) {
          reject(e);
        };
        reader.readAsDataURL(blob);
      });
    },

    async testTonSdk(isTwoSteps) {
      // 1. connect
      //  create sub providers for each namespace/chain
      let nameSpacesChains = [];
      let optSpaceChains = [];

      let solanaChains = [
        "solana:5eykt4UsFv8P8NJdTREpY1vzqKqZKvdp",
        "solana:4uhcVJyU9pJkvQyS88uRDiswHXSCkY3z",
        "sonic:4uhcVJyU9pJkvQyS88uRDiswHXSCkY3z",
      ];
      let hasEip155, hasSolana, hasSui, hasBtc, hasAptos, hasTon, hasCosmos = false

      let suiChains = [
        "sui:mainnet",
      ];

      let btcChains = [
        "btc:mainnet",
        "fractal:mainnet"
      ];

      const chains = Object.entries(this.availableChains)
          .filter((chain) => chain[1].checked)
          .map((chain) => chain[0]);
      console.log(`chains  ===> ${chains.values()}`)
      chains.forEach(function (chainId) {
        console.log(`chains.forEach chainId  ===> ${chainId}`)

        if (solanaChains.includes(chainId)) {
          nameSpacesChains.push(chainId);
          hasSolana = true;
          return;
        }
        if (typeof chainId == 'string' && chainId.startsWith("ton")) {
          nameSpacesChains.push(chainId)
          hasTon = true
          return
        }
        if (typeof chainId == 'string' && (chainId.startsWith("aptos") || chainId.startsWith("movement"))) {
          nameSpacesChains.push(chainId)
          hasAptos = true
          return
        }
        if (typeof chainId == 'string' && chainId.startsWith("svm")) {
          nameSpacesChains.push(chainId)
          hasSolana = true
          return
        }
        if (typeof chainId == 'string' && chainId.startsWith("cosmos")) {
          nameSpacesChains.push(chainId)
          hasCosmos = true
          return
        }

        if (suiChains.includes(chainId)) {
          console.log(`nameSpacesChains.push  ===> ${chainId}`);
          nameSpacesChains.push(chainId);
          hasSui = true;
          return;
        }

        if (btcChains.includes(chainId)) {
          console.log(`nameSpacesChains.push  ===> ${chainId}`)
          nameSpacesChains.push(chainId)
          hasBtc = true
          return
        }

        hasEip155 = true
        nameSpacesChains.push(`eip155:${chainId}`)
      })


      optionChains.forEach(function (chainId) {
        if (solanaChains.includes(chainId)) {
          optSpaceChains.push(chainId);
          return;
        }
        if (typeof chainId == "string" && chainId.startsWith("svm")) {
          nameSpacesChains.push(chainId);
          hasSolana = true;
          return;
        }
        if (suiChains.includes(chainId)) {
          optSpaceChains.push(chainId);
          return;
        }

        if (btcChains.includes(chainId)) {
          optSpaceChains.push(chainId)
          return
        }

        optSpaceChains.push(`eip155:${chainId}`)
      })

      console.log(`namespaceChains  ===> ${JSON.stringify(nameSpacesChains)}`);
      var connectData = {};
      if (nameSpacesChains && nameSpacesChains.length > 0) {
        var requireData = {};
        if (isTwoSteps){
          let includeEth = nameSpacesChains.includes("eip155:1")
          if (!includeEth){
            alert("请选择ETH ");
            return;
          }
        }

        if (hasEip155) {
          requireData.eip155 = {
            chains: nameSpacesChains.filter((item) =>
              item.startsWith("eip155")
            ),
            defaultChain: nameSpacesChains.filter((item) =>
              item.startsWith("eip155")
            )[0],
          };
        }
        if (hasSolana) {
          requireData.solana = {
            chains: nameSpacesChains.filter(
              (item) =>
                item.startsWith("solana") ||
                item.startsWith("sonic") ||
                item.startsWith("svm")
            ),
            defaultChain: nameSpacesChains.filter(
              (item) =>
                item.startsWith("solana") ||
                item.startsWith("sonic") ||
                item.startsWith("svm")
            )[0],
          };
        }

        if (hasCosmos) {
