            ton_addr:{
              name: 'ton_addr',
            }
          };
          if (this.tonProofValue){
            tonParams = {
              ton_addr:{
                name: 'ton_addr',
              },
              ton_proof:{
                name: 'ton_proof',
                payload: this.tonProofValue,
              }
            };
          }


          requireData.ton = {
