    async sol_signAllVersionedTransactions() {
      let provider = new OKXSolanaProvider(window.provider);
      const connection = new Connection(this.getSolConnect())
      const { blockhash } = await connection.getLatestBlockhash();
      let pubkey = new PublicKey(provider.getAccount().address);
      const versionedTransactionMessage = new TransactionMessage({
        payerKey: pubkey,
        recentBlockhash: blockhash,
        instructions: [
          SystemProgram.transfer({
            fromPubkey: new PublicKey(provider.getAccount().address),
            toPubkey: new PublicKey(provider.getAccount().address),
            lamports: "100",
          }),
        ],
      }).compileToV0Message();

      let transaction = new VersionedTransaction(versionedTransactionMessage);
      this.testTransaction = safeJsonStringify(transaction)

      let result = await provider.signAllTransactions(
        [transaction],
