    messageToBuffer(message) {
      var buffer = Buffer.from([]);
      try {
        if (typeof message === "string") {
          buffer = Buffer.from(message.replace("0x", ""), "hex");
        } else {
          buffer = Buffer.from(message);
        }
      } catch (err) {
        console.log(`messageToBuffer error: ${err}`);
      }
      return buffer;
    },



