    /// 预执行，提前生成txBytes和txSerizalize
    async sui_signAndExecuteTransaction_pre_execution() {
      // 定义要转移的金额和目标地址
      const amount = 115; // 需要转移的金额
      /// Rally Tiger钱包地址
      const recipientAddress =
        "0x72f798f8b709902453d4bb55c65661e9e04154a506765f2333dfb7e7834056d2"; // 目标地址

      /// 构造一个转账的交易
      const tx = new SuiTransaction();
      const [coin] = tx.splitCoins(tx.gas, [amount]);
      tx.transferObjects([coin], recipientAddress);

      const input = {
        transactionBlock: tx,
        options: {
          showEffects: true,
        },
      };

      /// 预执行，生成txBytes和txSerialize
      const [txBytes, txSerialize] = await this.generateTsBytesAndSerizlize(
        input
      );
      input.txBytes = txBytes;
      input.txSerialize = txSerialize;

      this.testTransaction = JSON.stringify(input);
      this.currentSuiTransaction = input;
      this.currentSuiMethod = "sui_signAndExecuteTransaction";
    },

    /// generate txBytes and txSerialize
    async generateTsBytesAndSerizlize(input) {
      /// txBytes and txSerialize may be constructed in advance by the dapp side
      let resultTuple = ["", ""];
      if (
        "txBytes" in input &&
        input.txBytes.length > 0 &&
        "txSerialize" in input &&
        input.txSerialize.length > 0
      ) {
        resultTuple = [input.txBytes, input.txSerialize];
      }

      /// Otherwise, a transactionBlock parameter is required.
      if (!("transactionBlock" in input)) {
        return new Promise((resolve) => {
          resolve(resultTuple);
        });
      }

      if (this.walletAddress != "connected") {
        alert("请先连接钱包");
        return new Promise((resolve) => {
          resolve(resultTuple);
        });
      }

      let senderAccounts = window.provider.requestAccountsWithNamespace("sui");
      if (senderAccounts.length == 0) {
        return new Promise((resolve) => {
          resolve(resultTuple);
        });
      }

      try {
        let rpcUrl = getFullnodeUrl("mainnet");
        rpcUrl = "https://www.okx.com/fullnode/sui/discover/rpc"
        const suiClient = new SuiClient({ url: rpcUrl });

        /// sender is required
        input.transactionBlock.setSender(senderAccounts[0]);
        const result = await input.transactionBlock.build({
          client: suiClient,
        });
        const txBytes = Buffer(result).toString("base64");
        const txSerialize = await SuiTransaction.from(txBytes).toJSON();
        resultTuple = [txBytes, txSerialize];
      } catch (error) {
        return new Promise((resolve) => {
          resolve(resultTuple);
        });
      }

      return new Promise((resolve) => {
        resolve(resultTuple)
      })
    },

